.cast {
  /* display: flex; */
  /* flex-wrap: wrap; */
  display: grid;
  max-width: calc(100vw - 48px);
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  grid-gap: 14px;
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  list-style: none;
  margin-left: auto;
  margin-right: auto;
}

.castItem {
  width: 200px;
}

.castImage {
  border-radius: 8px;
}
